	transition-delay: 4s;
}
/* firefox doesn't do scaleX very well for <img>s so will just fade it in */
@supports (-moz-appearance: none) and (text-align-last: auto) {
	.acronym tr.viewpoint {
		transform: scaleX(1);
	}
